import styled from 'styled-components';

export const StyledHeadline = styled.div`
    display: flex;
    align-items: center;
    position: absolute;
    width: 350px;
    left: calc(50vw - (350px / 2));
   

    h1{
        margin: 0 10px;
    }

    i {
        font-size: 28px;
        
    }
`